import React, { Component } from "react";

import {withParse} from '../../parse'
//  lets move the input to its own componet so that
//  it can be used to do a donated chat too 
class NewMessageInputBase extends Component{
    constructor(props){
        super(props)
        var _this = this;
        console.log(this.props.parse.parse.User.current())
        this.props.parse.parse.onAuthChange((user)=>{
            console.log(user)
            _this.setState({username:user.username});
        })
        this.state = {

            inputState : "message", //   message || superchat
            
            username : "",
            profilePicture : "",
            messageBody : "",
            maxLength : 200,
            superchatValue : 0, //  in coins 
            superchatValueSlider: 0,
            room : this.props.room
        }
    }
    //  it will be in gems 
    openSuperChatMenu(ammount=20){

    }
    onChange = event => {
        //  if the target value is bigger than max text then 
        //  this is the catch for the max length thing 
        if(event.target.name == "messageBody" &&event.target.value.length > this.state.maxLength){

            return;
        }
        
        if(event.target.name == "superchatValueSlider"){
            // change the number value based on the 
            //  slider value 

            //  so the slider will be diviede in differnt protuion 

            //  1-5 , 5
            var remainingSteps = event.target.value;
            var ActualValue = 0;
            while(remainingSteps > 0){
                if(remainingSteps > 48){
                    ActualValue = ActualValue + 500;
                }else if(remainingSteps > 31){
                    //  5-25 , 5 stap
                    ActualValue = ActualValue + 100;
                }else if(remainingSteps > 13){
                    //  25-150 . 6 steps
                    ActualValue = ActualValue + 50;
                }else if(remainingSteps > 10){
                    //  150-1000, steps of 50 17 steps
                    ActualValue = ActualValue + 25;
                }else if(remainingSteps > 5){
                    //  1000-2500 steps of 100 15 steps
                    ActualValue = ActualValue +5;
                }else {
                    //  rest is steps of 500 remainder
                    ActualValue = ActualValue + 1;
                }
                remainingSteps --;
            }
            
            
            
            this.setState({
                superchatValue : ActualValue
            })
            

            
            
            
        }
        this.setState({ [event.target.name]: event.target.value });
      };
    sendMessage(){
        //  get the stuff 
        
       
        const { messageBody, room,inputState,superchatValue } = this.state;
        if(messageBody.length == 0){
            return;
        }
        //  create a new message object 
        const ChatMessages = this.props.parse.parse.Object.extend("ChatMessages");
        const chatMessage = new ChatMessages();

        chatMessage.set("user", this.props.parse.parse.User.current());
        chatMessage.set("livechatroom", room);
        chatMessage.set("messageBody", messageBody);
        chatMessage.set("userProfile",this.props.parse.parse.User.current().attributes.publicProfile)
        if(inputState == "superchat"){
            //  is it a super chat so we should set the related feilds
            //  and let the cloud code handle it 
            chatMessage.set("superchat" , true)
            chatMessage.set("superchatValue" , superchatValue);
        }
        chatMessage.save().then((chatMessage) => {
        // Execute any logic that should take place after the object is saved.
        //console.log(chatMessage);
            this.setState({
                messageBody : ""
            })
        }, (error) => {
        // Execute any logic that should take place if the save fails.
        // error is a Parse.Error with an error code and message.
        alert('Failed to create new object, with error code: ' + error.message);
        });
        //  give it this room and user 

        //  set the body 


        //  check if a super chat, if so do nothiung


        //  if not just save the message object 

        // reset the inital state 
    }
    render(){
        var _this = this;
        const {  messageBody, maxLength ,superchatValue,superchatValueSlider} = this.state;

        //  if its a super chat we need to do a few things 

        //  fisrt we need to add some color arrown the user and message
        //  add the ammount below the user name, 

        //  second is the ammount with a slider at the bottom

        //  and swap out the old menue for the a new one that 

        //  says buy and send 
        var profilePicString = "https://picsum.photos/200"
        console.log(this.props.parse.parse.User.current())
        if(this.props.parse.parse.User.current().attributes.publicProfile.attributes.profilePicture){
            profilePicString = this.props.parse.parse.User.current().attributes.publicProfile.attributes.profilePicture;
        }

        var message_input = (
            <div className="live-chat-input-main">
                        <img src={profilePicString} className="live-chat-profile-pic" />
                    
                        <div className="live-chat-input-wapper">
                            <div  className="live-chat-message-username" style={{textAlign:"left"}}>
                                {this.props.username}
                            </div>
                            <textarea  
                            name="messageBody"
                            value={messageBody}
                            onChange={this.onChange}
                            className="live-chat-input-body"/>
                            
                        </div>
                    </div>
        );
        var superChatAmmount //= (<div></div>);
        if(this.state.inputState == "superchat"){
            message_input = (
                <div className = "">

                
                    <div className="live-chat-input-main superchat-main">
                            <img src="https://picsum.photos/200" className="live-chat-profile-pic superchat-profile-pic" />
                        
                            <div className="live-chat-input-wapper">
                                <div  className="live-chat-message-username live-chat-message-username-superchat" style={{textAlign:"left"}}>
                                {this.props.username}
                                </div>
                                <div className="live-chat-superchat-value" style={{textAlign:"left"}}>
                                    {superchatValue} Coins
                                </div>
                                
                            </div>
                        </div>
                        <textarea  
                        name="messageBody"
                        value={messageBody}
                        onChange={this.onChange}
                        rows={2}
                        className="live-chat-input-body live-chat-input-body-superchat"/>
                    </div>
            )
            superChatAmmount = (<div>
                <input type="number"
                 value={superchatValue} 
                 name="superchatValue"
                 onChange={this.onChange}
                />
                <input type="range" 
                value={superchatValueSlider}
                onChange={this.onChange} 
                name="superchatValueSlider" 
                min="0" 
                max="60"  
                step="1"></input>
            </div>)

        }
        


        return (
            
            <div className="live-chat-input">
                    {message_input}
                    {superChatAmmount}
                    <div className="live-chat-input-menu">
                    <div className="live-chat-menu-button">
                        <div className="live-chat-menu-button-icon" 
                            onClick={()=>{
                                _this.props.togglePopupMenu()
                            }}>

                        </div>
                    </div>
                             
                            <div className="live-chat-input-menu-button flex-grow-end">
                                <div className="live-chat-input-length-count">
                                   {messageBody.length} / {maxLength}
                                </div>
                                <div class="send-icon"
                                onClick={()=>{
                                    // console.log("asdasd")
                                    _this.sendMessage()
                                }}
                                >

                                </div>
                            </div>
                        </div>
                </div>
        )
    }
}
export default  withParse(NewMessageInputBase)