import React, { Component } from "react";

import './livestream.css';
import {withParse} from '../parse';
import NewMessageInput from './components/NewMessageInput';
import LiveChatMessages from './components/LiveChatMessages';
import {UserMenuWindow} from './components/chatUserSettings';
import CoinsMenu from './components/Coins';
const INITIAL_STATE = {
    username: '',
    email: '',
    phone: '',
    passwordOne: '',
    passwordTwo: '',
    error: null,
};
  

//  this componeet cna be used by itself for 
//  a stream so people can use it on another 
//  device 
export class LiveChatBase extends Component{
    constructor(props){
        
        super(props);
        var _this = this;
        console.log(props)
        this.state = {
            room : null,
            showTimeStamp : false,
            showSettingsMenu : false,
            showCoinMenu: false,
            roomid:"ObGzMj3G2E"
            
        }
        
        console.log(this.props.parse.parse.User.current() == null)
        if(this.props.parse.parse.User.current() == null){
            //  then we make an anoon user 
            console.log("creating annon user")
            this.props.parse.createNewAnonUser();
            this.state.noUser = true;
            this.props.parse.parse.onAuthChange((user)=>{
                console.log(user)
                _this.setState({noUser : false,username: user.attributes.username})
                _this.loadRoom();
            })
        }else{
            //  they are loged in 
            this.state.noUser = false;
            this.state.username = this.props.parse.parse.User.current().attributes.username
            this.loadRoom();
        }
    }
    async loadRoom(){
        var _this = this;
        //  will make a parse qurey after we know there is a user 
        //  and they can send messages 
        var LiveChatRoom = this.props.parse.parse.Object.extend("LiveChatRoom");
        var query = new this.props.parse.parse.Query(LiveChatRoom);
         await query.get(this.state.roomid).then((chatRoom) => {
             console.log(chatRoom)
            // The object was retrieved successfully.
            _this.setState({
                loadedRoom : true,
                room : chatRoom
            })
          }, (error) => {
            // The object was not retrieved successfully.
            // error is a Parse.Error with an error code and message.
            _this.setState({
                loadedRoom : false,
                errpr : "Could not load live chat "
            })
          });
    } 
    
    togglePopupMenu(){
        this.setState({menuOpen : !this.state.menuOpen})
    } 
    toggleTimeStampView(){
        this.setState({showTimeStamp : !this.state.showTimeStamp})
    }
    toggleSettingsMenu(){
        console.log("toggleingh menu")
        this.setState({showSettingsMenu : !this.state.showSettingsMenu})
    }
    toggleCoinMenu(){
        console.log("showing coin menu")
        this.setState({showCoinMenu : !this.state.showCoinMenu})
    }
    
    render(){
        var _this = this;
        
        if(!this.state.room ){
            return (<div className="live-chat-wrapper">
                <div className="live-chat-messages"
                    id="live-chat-messages" style={{justifyContent:"center",alignItems:"center", background: "white", overflowY : "hidden", color : "#333333"}} >
                        <img style={{height : "8rem",width:"8rem"}}
                        src={require('./assets/loading.svg')} />
                        <h3>Loading Chat...</h3>
                    </div>
                </div>)
        }
        return (
            <div className="live-chat-wrapper">
                <UserMenuWindow  
                visible={this.state.showSettingsMenu}
                toggleVisibility={()=>{
                    _this.toggleSettingsMenu();
                }}
                 />
                <CoinsMenu 
                visible={this.state.showCoinMenu}
                toggleVisibility={()=>{
                    _this.toggleCoinMenu();
                }}
                />
                < LiveChatPopupMenu 
                open={this.state.menuOpen}
                toggle={()=>{
                    _this.togglePopupMenu()
                }} 
                toggleTimeStampView={()=>{
                    _this.toggleTimeStampView();
                }}
                toggleSettingsMenu={()=>{
                    _this.toggleSettingsMenu();
                }}
                toggleCoinsMenu={()=>{
                    _this.toggleCoinMenu();
                }}
                />
                
                < LiveChatMessages room={this.state.room} showTimeStamp={this.state.showTimeStamp} />
                <NewMessageInput
                togglePopupMenu={()=>{
                    _this.togglePopupMenu()
                }}
                room={this.state.room} username={this.state.username} />
            </div>
        )
    }
}
export const LiveChat = withParse(LiveChatBase);

class LiveChatPopupMenu extends Component{
    constructor(props){
        super(props);
        this.state = {
            open : false
        }
    }
    toggle(){
        this.setState({open : !this.state.open});
    }
    
    render(){
        var _this = this;
        var classes = "live-chat-popup-menu-wrapper "
        if(this.props.open){
            classes = classes + "opened";
        }
        return (<div className={classes}>
                    <div className="live-chat-popup-menu shadow">
                        <div className="live-chat-popup-menu-item"
                        onClick={()=>{
                            _this.props.toggleTimeStampView();
                            _this.props.toggle()
                        }}
                        >
                            Toggle Timestamps
                        </div>
                        <div className="live-chat-popup-menu-item"
                        onClick={()=>{
                            _this.props.toggleSettingsMenu();
                            _this.props.toggle();
                        }}
                        >
                            Settings
                        </div>
                        <div className="live-chat-popup-menu-item"
                        onClick={()=>{
                            _this.props.toggleCoinsMenu();
                            _this.props.toggle();
                        }}
                        >
                            Get Coins
                        </div>
                        <div className="live-chat-popup-menu-item">
                            Give Feedback
                        </div>
                    </div>
                </div>)
    }
}