import React, { Component } from "react";
import './overlay.css'
import {withParse} from '../../parse'

export  class ChatOverlay extends Component{
    constructor(props){
        super(props);
    }
    
    render(){
        var _this = this;
        var className = "chat-window-overlay "
        
        if(this.props.visible ){
            
            className = className + " chat-window-overlay-open"
        }
        return (<div  className={className}>
                <div className="overlay-close-btn-wrapper"
                    
                >
                    <div className="overlay-close-btn"
                    onClick={()=>{
                        //  here we will exit the overlayu and go back 
                        _this.props.toggleVisibility()
                    }}
                >   </div>
                </div>
                    {this.props.children}
                </div>)
    }
}

const INITAL_STATE = {
    userName : "",
    
}
export  class UserMenuBase extends Component{
    constructor(props){
        super(props);
        console.log(this.props.parse.parse.User.current())
        this.state = {
            username : this.props.parse.parse.User.current().attributes.username
        }
    }
    onChange = event => {
        //  if the target value is bigger than max text then 
        //  this is the catch for the max length thing 
        
        this.setState({ [event.target.name]: event.target.value });
      };
    render(){
        const {username} = this.state;
        return (
        < ChatOverlay 
            visible={this.props.visible}
            toggleVisibility={this.props.toggleVisibility}
        >
        <UpdateUserName />
        </ChatOverlay>)
    }
}


class UpdateUserNameBase extends Component{
    constructor(props){
        super(props);
        this.state = {
            username : this.props.parse.parse.User.current().attributes.username
        }
    }
    onChange = event => {
        //  if the target value is bigger than max text then 
        //  this is the catch for the max length thing 
        
        this.setState({ [event.target.name]: event.target.value });
      };

    async onSubmit(){
        console.log(this.state)
        var user = this.props.parse.parse.User.current();
        user.set('username',this.state.username)
        console.log(user.attributes.publicProfile)
        user.attributes.publicProfile.set('username',this.state.username)
        try{await user.save();}catch(e){console.log(e)}
        
         
        console.log("yeet the user has been chaged")
    }
    render(){
        const { username } = this.state;
        var _this = this;
        return(
            <div>
                <h2>Change User Name</h2>
                <input
                name="username" 
                value={username} 
                onChange={this.onChange} />
                <button onClick={()=>{_this.onSubmit()}}>Change Username</button>
            </div>
        )
    }
}





const UpdateUserName = withParse(UpdateUserNameBase);



export const UserMenuWindow = withParse(UserMenuBase);