//  make sure we give it parse so it can substribe to message roomes

export default withParse(LiveChatMessagesBase);
const LiveChatMessage = (props)=>{
    var timestamp = props.timeStamp
    if(!props.showTimeStamp){
        timestamp = " "
    }
    var profilePicString = "https://picsum.photos/200"
        if(props.profilePicture){
            profilePicString = props.profilePicture;
        }
    return (<div className="live-chat-message">
                <div>
                        <img src={profilePicString} className="live-chat-profile-pic" />
                    </div>
                <div style={{
                    flexGrow : 1,
                    textAlign: "left"
                }} className="live-chat-message-text-wrapper">
                    <span className="live-chat-message-username">
                        {props.username} {timestamp}
                    </span>
                    <span className="live-chat-message-text">
                        {props.body}
                    </span>
                </div>
                
            </div>)
}


//  this supporting fucntion will give an appropriate 
//  time stamp for the value 

//  lets just show HH:MM PM / AM
function parseTimeStamp(timestamp){
    var h = new Date(timestamp).getHours();
    var m = new Date(timestamp).getMinutes();

    h = (h<10) ? '0' + h : h;
    m = (m<10) ? '0' + m : m;

    return h + ':' + m;
}


function superChatValueToColor(value){
    if(value < 10){
        return "blue"
    }else if(value < 25){
        return "red"
    }else if(value < 50){
        return "green"
    }else if(value < 100){
        return "yellow"
    }else if(value < 1000){
        return "gray"
    }else{
        return "black"
    }
}