//  now the actual feedback menu there 
class CoinsMenuBase extends Component{
    constructor(props){
        super(props);

        
        this.state = {
            payment : null
        }
    }
    async createPaymnet(){
        //  will ask out server to make a 
        //  paymnet object with either a deal id 
        //  or an ammont of coins 
        this.props.parse.parse.Cloud.run("");
    }
    render(){
        var innerElement = < CoinShop
        createPaymnet={this.createPaymnet}
        />;
        if(this.state.payment){
            innerElement = "asdasd"
        }
        return (<ChatOverlay
            visible={this.props.visible}
            toggleVisibility={this.props.toggleVisibility}


        >   
            
                {innerElement}
                </ChatOverlay>)
    }
}

